.sarcasm {
  color: white;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  font-size: 1.6rem;

  &__container {
    width: 70vw;
    height: 90vh;
    margin: 5vh 0;
    display: flex;
    flex-direction: column;
  }

  &__header {
    font-size: 6rem;
    font-weight: 100;
    text-align: center;
    text-decoration: underline;
    margin-bottom: 5rem;
  }

  &__description {
    margin-bottom: auto;
    font-size: 2rem;
  }

  &__textfield {
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 2rem;
  }

  &__actions {
    text-align: right;
    width: 100%;
  }

  &__button {
    border: 0.5px solid black;
    padding: 0.5rem 1rem;
  }

  &__textarea {
    width: 100%;
    height: 50rem;
    resize: none;
    padding: 1.5rem;
    border: none;

    font-family: inherit;

    &:active,
    &:focus {
      outline: none;
    }
  }
}
